import React, { useState, useEffect } from 'react'
import { graphql, Link } from 'gatsby'
import { Helmet } from "react-helmet"
import moment from 'moment'

import rehypeReact from 'rehype-react'
import DetectNetworkConnectivityDemo from '../markdown-pages/2019-12-05-detect-network-connectivity/Demo'
import BlogHeader from '../components/BlogHeader'
import { useFirebase } from "gatsby-plugin-firebase"

import classNames from 'classnames'
import styles from './blogPost.module.scss'

const Template = ({ data, pageContext }) => {
  const { next, prev, edge } = pageContext
  const title = data.markdownRemark.frontmatter.title
  const html = data.markdownRemark.html
  const htmlAst = data.markdownRemark.htmlAst

  const [viewCount, updateViewCount] = useState(null)

  useFirebase(firebase => {
    firebase
      .database()
      .ref(`${edge.node.id}/viewCount`)
      .once('value')
      .then(snapshot => {
        if (snapshot.val()) {
          const nextValue = parseInt(snapshot.val()) + 1
          firebase
            .database()
            .ref(edge.node.id)
            .set({
              viewCount: nextValue
            })
          updateViewCount(nextValue)
        } else {
          firebase
            .database()
            .ref(edge.node.id)
            .set({
              viewCount: 1
            })
          updateViewCount(1)
        }
      })
  }, [])

  // useFirebase(firebase => {
  //   firebase
  //     .database()
  //     .ref(`${edge.node.id}/viewCount`)
  //     .on('value', snapshot => {
  //       if (snapshot.val()) {
  //         const currentValue = parseInt(snapshot.val())
  //         updateViewCount(currentValue)
  //       }
  //     })
  // }, [])

  const PostViewCount = () => {
    return (
      <span className={styles.viewCount}>{viewCount || '-'} views</span>
    )
  }

  const renderAst = new rehypeReact({
    createElement: React.createElement,
    components: {
      "post-view-count": PostViewCount,
      'demo-detect-network-connectivity': DetectNetworkConnectivityDemo,
    }
  }).Compiler

  const { frontmatter } = edge.node

  return (
    <div className={styles.container}>
      <BlogHeader />
      <div className={styles.content}>
        {/* <h1>{title}</h1> */}
        <Helmet>
          <meta charSet="utf-8" />
          <title>{title}</title>
          {/* <link rel="canonical" href="http://mysite.com/example" /> */}
        </Helmet>
        <div className={styles.content}>
        <h1 className={styles.postTitle}>{frontmatter.title}</h1>
        <div className={styles.postInfo}>
          <span className={styles.postedAt}>{moment(frontmatter.date).format('ll')}</span>
          <span className={styles.centerDot}>&#xB7;</span>
          <span className={styles.timeToRead}>{edge.node.timeToRead} min read</span>
          <span className={styles.centerDot}>&#xB7;</span>
          <span className={styles.viewCount}>{viewCount} view{viewCount > 1 ? 's' : ''}</span>
        </div>
        {
        frontmatter.tags && frontmatter.tags.length ? (
          <div className={styles.tags}>
            {
              frontmatter.tags.map(tag => (
                <Link className={classNames([styles.link, styles.tag])} key={tag} to={`/tags/${tag}`}>
                  #{tag}
                </Link>
              ))
            }
          </div>
        ) : null
      }

        <div className='blogpost markdown-body'>
          {renderAst(edge.node.htmlAst)}
        </div>
        {next && <Link to={next.frontmatter.path}>Next</Link>}
        {prev && <Link to={prev.frontmatter.path}>Prev</Link>}
      </div>
        </div>
    </div>
  )
}

export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: {eq: $pathSlug} }) {
      html
      frontmatter {
        title
      }
    }
  }
`

export default Template